@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&family=Lato&display=swap);
.title {
  text-align: center;
  font-size: 72px;
  color: #2f9c95;
  margin: 10px 0;
}
@media (max-width: 375px) {
  .title {
    font-size: 64px;
  }
}

.todo-input-wrapper input {
  width: 100%;
  padding: 10px;
  border-radius: 4px;
  border: 3px solid #2f9c95;
  font-family: "Indie Flower", cursive;
  font-size: 24px;
}

.todo-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-size: 24px;
  border-bottom: 3px solid #2f9c95;
  padding-top: 20px;
  padding-bottom: 10px;
}
.completed {
  text-decoration: line-through;
}
.mark {
  color: #2f9c95;
}
.fa-times {
  color: #664147;
}

.todo-list {
  padding: 0;
  margin: 40px 0;
}

.amount {
  display: inline-block;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  margin: 20px 0;
}
.amount::first-letter {
  color: #2f9c95;
  font-size: 18px;
  margin-right: 5px;
}
.btn-group {
  display: -webkit-flex;
  display: flex;
  margin-top: 20px;
}
.filter-btn {
  background-color: #40c9a2;
  padding: 5px 10px;
  margin-right: 20px;
  border: 2px solid transparent;
  border-radius: 4px;
  font-size: 18px;
}
.filter-btn.active,
.filter-btn:hover {
  border: 3px solid #2f9c95;
}
@media (max-width: 375px) {
  .filter-btn {
    font-size: 14px;
    margin-right: 10px;
  }
}

.todo-wrapper {
  width: 600px;
  margin: 0 auto;
  padding: 40px;
  border-radius: 10px;
  background-color: #e5f9e0;
  box-shadow: 5px 5px 15px 0px #bbb;
}
@media (max-width: 768px) {
  .todo-wrapper {
    width: 90%;
  }
}

* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 20px 0;
  font-family: "Indie Flower", cursive;
}

